<template>
  <div class="historical-data">
    <div class="layoutBox" style="width:30%;float:left;     padding-right: 10px;">
      <el-select v-model="stcd" placeholder='请选择'>
        <el-option v-for="item in stcds" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div class="toolbar-wrap">
      <span>时间选择 :</span>
      <div class="layoutBox" style="width:400px;">
        <el-date-picker v-model="dateRange" type="daterange" 
         format = 'yyyy-MM-dd HH:mm:ss'
         value-format = 'yyyy-MM-dd HH:mm:ss'
        start-placeholder="开始日期"
         end-placeholder="结束日期"></el-date-picker>
      </div>&emsp;
      <el-button  @click="getstationdatachart()" >
        <i class="iconfont icon-sousuo"></i>查询
      </el-button>
    </div>
    <div class="content-wrap">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="测量液位" name="1">
          <div style="height:100%;" v-loading="loading" element-loading-text="加载中" element-loading-spinner="loading-type-a" element-loading-background="rgba(255, 255, 255, 1)">
            <div id="chart1"> </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="珠江液位" name="2">
          <div style="height:100%;" v-loading="loading" element-loading-text="加载中" element-loading-spinner="loading-type-a" element-loading-background="rgba(255, 255, 255, 1)">
            <div id="chart2"> </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="过载倍速" name="3">
          <div style="height:100%;" v-loading="loading" element-loading-text="加载中" element-loading-spinner="loading-type-a" element-loading-background="rgba(255, 255, 255, 1)">
            <div id="chart3"> </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="溢流风险" name="4">
          <div style="height:100%;" v-loading="loading" element-loading-text="加载中" element-loading-spinner="loading-type-a" element-loading-background="rgba(255, 255, 255, 1)">
            <div id="chart4"> </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  name: "historicalData",
  data() {
    return {
      activeName: '1',
      loading: true,
      stcd: '',
      type: "1",
      chart_1: null,
      chart_2: null,
      chart_3: null,
      chart_4: null,
      dataObjecharts: null,
 
      dateRange: [new Date(new Date()-48*60*60*1000) , new Date()],
     


      rawData: false,
      tableData: [{
        date: '2019-08-10 12时',
        factor: '1.8'
      }],
    }
  },
  props: {
    stcds: Array
  },

  watch: {
  stcds:{ 
      immediate: true,  
      handler (newV, oldV) {
      console.log(newV)
       if(newV.length>0)
      {
       this.stcd=newV[0].value
   
      }
      },
      deep:true
     }
},

 


  methods: {

    getstationdatachart()
    {
  

     this. startup_echarts(this.stcd,this.dateRange[0] ,this.dateRange[1] );


    },

    startup_echarts(stcd,stm, etm) {

      this.dataObjecharts = new Object();

      var _seit = this;
      this.$http.get("./static/iot/site_mgmt/api/site/quote/basic/81211026/81211026.json", {
        params: {
          stcd: stcd

        }
      }).then((response) => {
        if (response.data.success === true) {

          var list = response.data.data;
          var flag = 0;
          list.forEach(function(item, index) {
            _seit.dataObjecharts[item.pn] = item.val;
            if (flag == 0) {
              _seit.dataObjecharts['dtmel'] = item.dtmel;
              _seit.dataObjecharts['stnm'] = item.stnm;
            }
            flag++;
          });



          _seit.$http.get("./static/iot/data_monitor/api/monitor-service/stMeasInfo.json", {
            params: {
              stcd: stcd

            }
          }).then((response) => {

            var list = response.data.data;

            list.forEach(function(item, index) {

              if (item.NM == '水深' || item.NM.indexOf('水深') >= 0) {
                _seit.ST_MEAS_ID = item.ST_MEAS_ID;
              }

            })

            _seit.createChart(stcd, stm, etm);

          })



        }
      }).catch((error) => {
        console.log(error)
      });


    },




    createChart: function(stcd, stm, etm) {
      var _that = this;
      if (!this.chart_1) {
        this.chart_1 = this.$echarts.init(
          document.getElementById("chart" + this.type)
        );

      }

      this.$http.get("./static/iot/grap_chart/station/chart20211127-20211129.json", {
        params: {
          stcd: stcd,
          stm: stm,
          etm: etm
        }
      }).then((response) => {
        if (response.data.success === true) {
          var list = response.data.data;
          _that.listData = _.cloneDeep(list);
          _that.updateChart1(list);
           this.loading = false;
        }

      }).catch((error) => {
        console.log(error)
      });

    },

    updateChart1: function(list) {



      if (this.chart_1) {
        this.chart_1.dispose();
      }


      this.chart_1 = this.$echarts.init(
        document.getElementById("chart" + this.type)
      );


      var _that = this;


      var listY = ['地面线', '预警线', '提醒线', '管顶线', '水深线', '井底线'];
      var listColor = ['#86B4EB', '#F97CBA', '#FFDF98', '#999966', '#2EC7C9', '#AA8463'];
      var listTm = new Array();
      var listZ = new Array();
      var listTop = new Array();
      var listWarn = new Array();
      var listInfo = new Array();
      var listPipe = new Array();
      var listBottom = new Array();

      if (_that.dataObjecharts.dtmel == null || typeof(_that.dataObjecharts.dtmel) == 'undefined')
        _that.dataObjecharts.dtmel = 0;
      var dTop = null;
      if (_that.dataObjecharts.top == null || typeof(_that.dataObjecharts.top) == 'undefined') {
        dTop = null;
      } else {


        dTop = (_that.type == '1' ? _that.dataObjecharts.top : (_that.dataObjecharts.top + _that.dataObjecharts.dtmel));
      }
      var dWarn = null;
      if (_that.dataObjecharts.warn == null || typeof(_that.dataObjecharts.warn) == 'undefined') {
        dWarn = null;
      } else {
        dWarn = (_that.type == '1' ? _that.dataObjecharts.warn : (_that.dataObjecharts.warn + _that.dataObjecharts.dtmel));
      }
      var dPipe = null;
      if (_that.dataObjecharts.pipe == null || typeof(_that.dataObjecharts.pipe) == 'undefined') {
        dPipe = null;
      } else {
        dPipe = (_that.type == '1' ? _that.dataObjecharts.pipe : (_that.dataObjecharts.pipe + _that.dataObjecharts.dtmel));
      }
      var dBottom = (_that.type == '1' ? 0 : _that.dataObjecharts.dtmel);
      var dInfo = null;
      if (_that.dataObjecharts.info == null || typeof(_that.dataObjecharts.info) == 'undefined') {
        dInfo = null;
      } else {
        dInfo = (_that.type == '1' ? _that.dataObjecharts.info : (_that.dataObjecharts.info + _that.dataObjecharts.dtmel));
      }

      for (var i = 0; i < list.length; i++) {
        var model = list[i];
        listTm.push(model.tm);
        var dZ = model.val;
        if (_that.type == '1') {

          var valInfo = dZ;
          if (valInfo < 0)
            dZ = 0;
          else
            dZ = valInfo;
        }
        listZ.push(dZ);
        listTop.push(dTop);
        listWarn.push(dWarn);
        listInfo.push(dInfo);
        listPipe.push(dPipe);
        listBottom.push(dBottom);
      }



      var that = this;
      var stnm = that.dataObjecharts.stnm;
      var option = {
        title: {
          text: (typeof(stnm) == 'undefined' ? '' : stnm) + '水深过程线',
          target: 'self',
          x: 'center',
          top: -3,
          textStyle: { fontSize: 12, fontWeight: 'bolder', color: '#333' }
        },
        tooltip: {
          position: function(point, params, dom, rect, size) {
            var width1Mid = dom.parentElement.clientWidth / 2;
            var width2 = dom.clientWidth;
            var x = point[0];
            if (x > width1Mid) {
              x = x - width2 - 20;
            } else {
              x = x + 20
            }
            return [x, 50];
          },
          trigger: 'axis',
          formatter: function(params) {
            var name = '';
            var strInf = '';
            if (params.length > 0) {
              for (var i = 0; i < params.length; i++) {
                if (i == 0) {
                  name = params[i].name;
                }
                var strVal = params[i].value + '';
                if (strVal.indexOf('.') >= 0 && strVal.length > 8) {
                  //格式化一下
                  try {
                    strVal = Number(strVal).toFixed(2);
                    strVal = Number(strVal);
                  } catch (ex) {}
                }
                if (strVal && Number(strVal) > 0) {
                  strVal = Number(strVal).toFixed(2);
                  strVal = Number(strVal);
                }
                if (strVal == "undefined") {
                  strVal = "--";
                }
                var unit = "";
                if (params[i].seriesName == "水深线" || params[i].seriesName == "管顶线" ||
                  params[i].seriesName == "预警线" || params[i].seriesName == "提醒线" ||
                  params[i].seriesName == "地面线" || params[i].seriesName == "井底线"
                ) {
                  unit = "(m)";
                } else if (params[i].seriesName == "过流倍数" || params[i].seriesName == "昨日过流倍数") {
                  unit = "(倍)";
                } else if (params[i].seriesName == "溢流风险" || params[i].seriesName == "昨日溢流风险") {
                  unit = "(%)";
                }

                strInf += '<br/>' + params[i].seriesName + unit + ':' + strVal;
              }
            }
            return name + strInf;
          },
          confine: true,
          padding: 2,
          textStyle: {
            fontSize: 12
          },
          backgroundColor: 'rgba(85, 85, 85, 0.83)'

        },
        legend: {
          data: listY,
          x: 'center',
          y: 'top',
          top: 20
        },
        toolbox: {
          show: false
        },
        grid: {
          top: 45,
          left: '3%',
          right: '4%',
          bottom: '7%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          data: listTm
        }],
        yAxis: [{
          type: 'value',
          scale: true
        }],
        series: []
      };

      for (var i = 0; i < listY.length; i++) {
        var lineType = "solid";
        if (i != 0) {
          lineType = "dashed";
        }
        var seri = {
          name: listY[i],
          type: 'line',
          showAllSymbol: true,
          smooth: true,
          itemStyle: {
            normal: {
              color: listColor[i],
              lineStyle: { width: 2, type: lineType },
              label: {
                show: false,
                position: 'right',
                textStyle: { fontStyle: 'bolder', fontSize: 12 }
              }
            }
          },
          symbol: 'emptyCircle',
          symbolSize: ((i == 0) ? 2 : 1),
          data: (i == 0 ? listTop : (i == 1 ? listWarn : (i == 2 ? listInfo : (i == 3 ? listPipe : (i == 4 ? listZ : (i == 5 ? listBottom : null))))))
        }
        option.series.push(seri);
      }
      if (this.type == "3") {
        //过流倍数“和“昨日过流倍数
        var seri = this.getTempSeri("过流倍数");
        option.series.push(seri);
        //              option.legend.data.push("过流倍数");
        var seri = this.getTempSeri("昨日过流倍数");
        option.series.push(seri);
        //              option.legend.data.push("昨日过流倍数");

      } else if (this.type == "4") {
        //溢流风险”和“昨日溢流风险 
        var seri = this.getTempSeri("溢流风险");
        option.series.push(seri);
        //              option.legend.data.push("溢流风险");
        var seri = this.getTempSeri("昨日溢流风险");
        option.series.push(seri);
        //              option.legend.data.push("昨日溢流风险");
      }

      this.chart_1.setOption(option);

      window.addEventListener("resize", function() {
        that.chart_1.resize();
      });
     // this.loading = false;
    },


