          target: 'self',
          x: 'center',
          top: -3,
          textStyle: { fontSize: 12, fontWeight: 'bolder', color: '#333' }
        },
        tooltip: {
          position: function(point, params, dom, rect, size) {
            var width1Mid = dom.parentElement.clientWidth / 2;
            var width2 = dom.clientWidth;
            var x = point[0];
            if (x > width1Mid) {
              x = x - width2 - 20;
            } else {
              x = x + 20
            }
            return [x, 50];
          },
          trigger: 'axis',
          formatter: function(params) {
            var name = '';
            var strInf = '';
            if (params.length > 0) {
              for (var i = 0; i < params.length; i++) {
                if (i == 0) {
                  name = params[i].name;
                }
                var strVal = params[i].value + '';
                if (strVal.indexOf('.') >= 0 && strVal.length > 8) {
                  //格式化一下
                  try {
                    strVal = Number(strVal).toFixed(2);
                    strVal = Number(strVal);
                  } catch (ex) {}
                }
                if (strVal && Number(strVal) > 0) {
                  strVal = Number(strVal).toFixed(2);
                  strVal = Number(strVal);
                }
                if (strVal == "undefined") {
                  strVal = "--";
                }
                var unit = "";
                if (params[i].seriesName == "水深线" || params[i].seriesName == "管顶线" ||
                  params[i].seriesName == "预警线" || params[i].seriesName == "提醒线" ||
                  params[i].seriesName == "地面线" || params[i].seriesName == "井底线"
                ) {
                  unit = "(m)";
                } else if (params[i].seriesName == "过流倍数" || params[i].seriesName == "昨日过流倍数") {
                  unit = "(倍)";
                } else if (params[i].seriesName == "溢流风险" || params[i].seriesName == "昨日溢流风险") {
                  unit = "(%)";
                }

                strInf += '<br/>' + params[i].seriesName + unit + ':' + strVal;
              }
            }
            return name + strInf;
          },
          confine: true,
          padding: 2,
          textStyle: {
            fontSize: 12
          },
          backgroundColor: 'rgba(85, 85, 85, 0.83)'

        },
        legend: {
          data: listY,
          x: 'center',
          y: 'top',
          top: 20
        },
        toolbox: {
          show: false
        },
        grid: {
          top: 45,
          left: '3%',
          right: '4%',
          bottom: '7%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          data: listTm
        }],
        yAxis: [{
          type: 'value',
          scale: true
        }],
        series: []
      };

      for (var i = 0; i < listY.length; i++) {
        var lineType = "solid";
        if (i != 0) {
          lineType = "dashed";
        }
        var seri = {
          name: listY[i],
          type: 'line',
          showAllSymbol: true,
          smooth: true,
          itemStyle: {
            normal: {
              color: listColor[i],
              lineStyle: { width: 2, type: lineType },
              label: {
                show: false,
                position: 'right',
                textStyle: { fontStyle: 'bolder', fontSize: 12 }
              }
            }
          },
          symbol: 'emptyCircle',
          symbolSize: ((i == 0) ? 2 : 1),
          data: (i == 0 ? listTop : (i == 1 ? listWarn : (i == 2 ? listInfo : (i == 3 ? listPipe : (i == 4 ? listZ : (i == 5 ? listBottom : null))))))
        }
        option.series.push(seri);
      }
      if (this.type == "3") {
        //过流倍数“和“昨日过流倍数
        var seri = this.getTempSeri("过流倍数");
        option.series.push(seri);
        //              option.legend.data.push("过流倍数");
        var seri = this.getTempSeri("昨日过流倍数");
        option.series.push(seri);
        //              option.legend.data.push("昨日过流倍数");

      } else if (this.type == "4") {
        //溢流风险”和“昨日溢流风险 
        var seri = this.getTempSeri("溢流风险");
        option.series.push(seri);
        //              option.legend.data.push("溢流风险");
        var seri = this.getTempSeri("昨日溢流风险");
        option.series.push(seri);
        //              option.legend.data.push("昨日溢流风险");
      }

      this.chart_3.setOption(option);
      window.addEventListener("resize", function() {
        that.chart_3.resize();
      });
    //  this.loading = false;

    },


    updateChart4: function(list) {


      if (this.chart_4) {
        this.chart_4.dispose();
      }
      this.chart_4 = this.$echarts.init(
        document.getElementById("chart" + this.type)
      );


      var _that = this;
      var listY = ['地面线', '预警线', '提醒线', '管顶线', '水深线', '井底线'];
      var listColor = ['#86B4EB', '#F97CBA', '#FFDF98', '#999966', '#2EC7C9', '#AA8463'];
      var listTm = new Array();
      var listZ = new Array();
      var listTop = new Array();
      var listWarn = new Array();
      var listInfo = new Array();
      var listPipe = new Array();
      var listBottom = new Array();

      if (_that.dataObjecharts.dtmel == null || typeof(_that.dataObjecharts.dtmel) == 'undefined')
        _that.dataObjecharts.dtmel = 0;
      var dTop = null;
      if (_that.dataObjecharts.top == null || typeof(_that.dataObjecharts.top) == 'undefined') {
        dTop = null;
      } else {
        dTop = (_that.type == '1' ? _that.dataObjecharts.top : (_that.dataObjecharts.top + _that.dataObjecharts.dtmel));
      }
      var dWarn = null;
      if (_that.dataObjecharts.warn == null || typeof(_that.dataObjecharts.warn) == 'undefined') {
        dWarn = null;
      } else {
        dWarn = (_that.type == '1' ? _that.dataObjecharts.warn : (_that.dataObjecharts.warn + _that.dataObjecharts.dtmel));
      }
      var dPipe = null;
      if (_that.dataObjecharts.pipe == null || typeof(_that.dataObjecharts.pipe) == 'undefined') {
        dPipe = null;
      } else {
        dPipe = (_that.type == '1' ? _that.dataObjecharts.pipe : (_that.dataObjecharts.pipe + _that.dataObjecharts.dtmel));
      }
      var dBottom = (_that.type == '1' ? 0 : _that.dataObjecharts.dtmel);
      var dInfo = null;
      if (_that.dataObjecharts.info == null || typeof(_that.dataObjecharts.info) == 'undefined') {
        dInfo = null;
      } else {
        dInfo = (_that.type == '1' ? _that.dataObjecharts.info : (_that.dataObjecharts.info + _that.dataObjecharts.dtmel));
      }

      for (var i = 0; i < list.length; i++) {
        var model = list[i];
        listTm.push(model.tm);
        var dZ = model.val;
        if (_that.type == '1') {
          //原始是带高程的还需要减掉
          //var valInfo=dZ-_that.dataObj.dtmel;
          var valInfo = dZ;
          if (valInfo < 0)
            dZ = 0;
          else
            dZ = valInfo;
        }
        listZ.push(dZ);
        listTop.push(dTop);
        listWarn.push(dWarn);
        listInfo.push(dInfo);
        listPipe.push(dPipe);
        listBottom.push(dBottom);
      }
      var that = this;
      var stnm = that.dataObjecharts.stnm;
      var option = {
        title: {
          text: (typeof(stnm) == 'undefined' ? '' : stnm) + '水深过程线',
          target: 'self',
          x: 'center',
          top: -3,
          textStyle: { fontSize: 12, fontWeight: 'bolder', color: '#333' }
        },
        tooltip: {
          position: function(point, params, dom, rect, size) {
            var width1Mid = dom.parentElement.clientWidth / 2;
            var width2 = dom.clientWidth;
            var x = point[0];
            if (x > width1Mid) {
              x = x - width2 - 20;
            } else {
              x = x + 20
            }
            return [x, 50];
          },
          trigger: 'axis',
          formatter: function(params) {
            console.log(params)
            var name = '';
            var strInf = '';
            if (params.length > 0) {
              for (var i = 0; i < params.length; i++) {
                if (i == 0) {
                  name = params[i].name;
                }
                var strVal = params[i].value + '';
                if (strVal.indexOf('.') >= 0 && strVal.length > 8) {
                  //格式化一下
                  try {
                    strVal = Number(strVal).toFixed(2);
                    strVal = Number(strVal);
                  } catch (ex) {}
                }
                if (strVal && Number(strVal) > 0) {
                  strVal = Number(strVal).toFixed(2);
                  strVal = Number(strVal);
                }
                if (strVal == "undefined") {
                  strVal = "--";
                }
                var unit = "";
                if (params[i].seriesName == "水深线" || params[i].seriesName == "管顶线" ||
                  params[i].seriesName == "预警线" || params[i].seriesName == "提醒线" ||
                  params[i].seriesName == "地面线" || params[i].seriesName == "井底线"
                ) {
                  unit = "(m)";
                } else if (params[i].seriesName == "过流倍数" || params[i].seriesName == "昨日过流倍数") {
                  unit = "(倍)";
                } else if (params[i].seriesName == "溢流风险" || params[i].seriesName == "昨日溢流风险") {
                  unit = "(%)";
                }

                strInf += '<br/>' + params[i].seriesName + unit + ':' + strVal;
              }
            }
            return name + strInf;
          },
          confine: true,
          padding: 2,
          textStyle: {
            fontSize: 12
          },
          backgroundColor: 'rgba(85, 85, 85, 0.83)'

        },
        legend: {
          data: listY,
          x: 'center',
          y: 'top',
          top: 20
        },
        toolbox: {
          show: false
        },
        grid: {
          top: 45,
          left: '3%',
          right: '4%',
          bottom: '7%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          data: listTm
        }],
        yAxis: [{
          type: 'value',
          scale: true
        }],
        series: []
      };

      for (var i = 0; i < listY.length; i++) {
        var lineType = "solid";
        if (i != 0) {
          lineType = "dashed";
        }
        var seri = {
          name: listY[i],
          type: 'line',
          showAllSymbol: true,
          smooth: true,
          itemStyle: {
            normal: {
              color: listColor[i],
              lineStyle: { width: 2, type: lineType },
              label: {
                show: false,
                position: 'right',
                textStyle: { fontStyle: 'bolder', fontSize: 12 }
              }
            }
          },
          symbol: 'emptyCircle',
          symbolSize: ((i == 0) ? 2 : 1),

          data: (i == 0 ? listTop : (i == 1 ? listWarn : (i == 2 ? listInfo : (i == 3 ? listPipe : (i == 4 ? listZ : (i == 5 ? listBottom : null))))))
        }
        option.series.push(seri);
      }
      // console.log(this.type)
      if (this.type == "3") {
        //过流倍数“和“昨日过流倍数
        var seri = this.getTempSeri("过流倍数");
        option.series.push(seri);
        //              option.legend.data.push("过流倍数");
        var seri = this.getTempSeri("昨日过流倍数");
        option.series.push(seri);
        //              option.legend.data.push("昨日过流倍数");

      } else if (this.type == "4") {
        //溢流风险”和“昨日溢流风险 
        var seri = this.getTempSeri("溢流风险");

        option.series.push(seri);
        //option.legend.data.push("溢流风险");
        var seri = this.getTempSeri("昨日溢流风险");
        option.series.push(seri);
        // option.legend.data.push("昨日溢流风险");
      }



      this.chart_4.setOption(option);
      window.addEventListener("resize", function() {
        that.chart_4.resize();
      });
  

    },

   getTempSeri: function(name) {
      var seri = {
        name: name,
        type: 'line',
        smooth: true,
        itemStyle: {
          normal: {
            lineStyle: { width: 2, type: 'dashed' },
            label: {
              show: false,
              position: 'right',
              textStyle: { fontStyle: 'bolder', fontSize: 12 }
            }
          }
        },
        symbol: 'emptyCircle',
        symbolSize: 1,
        data: []
      }
      return seri;
    },
     handleClick(tab, event) {
     this.type = tab.name;
       this.loading = true;
      if (tab.name == "1") {
        this.updateChart1(this.listData);
        setTimeout(() => {

          this.chart_1.resize();
           this.loading = false;
        }, 10)


      }
      if (tab.name == "2") {
       
        this.updateChart2(this.listData);
        setTimeout(() => {

          this.chart_2.resize();
           this.loading = false;
        }, 10)
      }
      if (tab.name == "3") {
        this.updateChart3(this.listData);
        setTimeout(() => {

          this.chart_3.resize();
           this.loading = false;
        }, 10)
      }
      if (tab.name == "4") {
        this.updateChart4(this.listData);
        setTimeout(() => {
   
          this.chart_4.resize();
              this.loading = false;
        }, 10)
      }

    }


  },
  mounted() {
     //48小时 
 

      var stm=new Date(new Date()-48*60*60*1000).toFormatStr(10);
      var etm=new Date().toFormatStr(10);

      this.startup_echarts(this.stcd, stm, etm);
  }
}

</script>
<style scoped lang="less">
.historical-data {
  height: 100%;

  .content-wrap {
    height: calc(100% - 30px);

    #trends-historical {
      height: 55%;
    }

    .table-wrap {
      height: 45%;
    }
  }
}

</style>



