   <div style="height:calc(100% - 0px)">
    <el-scrollbar style="height:100%">
     <yf-tabs type="border-card" style="height:100%">
 
    <yf-tab-pane label="预警" style="height:100%"  >
 

    <div class="alarm-modular modular-box">
    
      <div class="modular-body">
        <el-scrollbar style="height: 100%">
          <ul class="alert-list">
            <li v-for="(item,index) in currentData" :key="index">
              <el-row>
                <el-col :span="8">
                  <p>
                    <span class="label-text">测站名称</span>
                  </p>
                  <p>
                    <i class="el-icon-warning wrnicon"></i>
                    <span class="site-name">{{ item.stMeas }}</span>
                  </p>
                </el-col>
                <el-col :span="3">
                  <p>
                    <span class="label-text">测站规则</span>
                  </p>
                  <p>
                    <span>{{ item.ruleNm }}</span>
                  </p>
                </el-col>
                <el-col :span="5">
                  <p>
                    <span class="label-text">开始时间</span>
                  </p>
                  <p>
                    <span>{{ item.startTm }}</span>
                  </p>
                </el-col>
                <el-col :span="8">
                  <el-button type="secondBtn" @click="showalarmDialog=true">预警设置</el-button>
                  <el-button type="secondBtn">历史预警</el-button>
                  <el-button type="secondBtn">预警发送</el-button>
                  <el-button type="secondBtn">关闭预警</el-button>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="24">
                  <div class="label-state-wrap">
                    <span class="label-state state-b">{{ item.statusstr }}</span>
                    <span class="label-state state-a" v-if="item.status === 'init'  ">持续中</span> 
                    <span class="label-state state-a" v-if="item.status === 'close'   ">已处理</span>
                    <span class="label-state   state-c" @click='showwrmDialogFrim(item.id)'>历史记录</span>
                    <!-- <span class="label-state state-b" v-if="item.handleState === 0">未响应</span>
                    <span class="label-state" v-if="item.handleState === 1">已响应</span>
                    <span class="label-state state-a" v-if="item.handleState === 2">已处理</span>-->
                  </div>
                </el-col>
              </el-row>
            </li>
          </ul>
          <div class="table-pagination-wrap">
            <el-pagination @size-change="paginationSizeChange" @current-change="paginationChange" :current-page.sync="currentPage" :page-size="pageSize" popper-class="select-pagination-option" background :small="true" :page-sizes="pageList" layout="total, sizes, slot,->,prev, pager, next, jumper" :total="total">
              <template v-slot:default>
                <span class="el-pagination__current">当前第{{ currentPage }}/{{totalPageNumber}}页</span>
              </template>
            </el-pagination>
          </div>
        </el-scrollbar>
      </div>
    </div>


    </yf-tab-pane>

     <yf-tab-pane label="故障报警">
            <div style="padding:10px">
              活动日历
            </div>
     </yf-tab-pane>
      <yf-tab-pane label="提醒">
            <div style="padding:10px">
              活动日历
            </div>
          </yf-tab-pane>




    </yf-tabs>
   </el-scrollbar>
  </div>




    <el-dialog width="1000px" :visible.sync="showwrmDialog" custom-class="yjDialog">
      <div slot="title" class="head_title">
        <p>
          报警管理
        </p>
        <div class="t_nav">
          <ul class="clearfix">
            <li @click="dialogNavClick(key)" v-for="(item,key) in dialogNav" :key="key" :class="dialogNavIndex>=key?'current':''">
              <i>{{key+1}}</i>
              {{item.name}}
            </li>
          </ul>
        </div>
      </div>
      <div class="dialogContent">
        <div class="leftBox width100" v-if="dialogNavIndex == 0">
          <el-timeline>
            <el-timeline-item icon="el-icon-message-solid" type='primary' size='large' :timestamp="currentwrnLogDetail.nowTm?currentwrnLogDetail.nowTm:''" placement="top">
              <el-card>
                <h4>实时信息</h4>
                <p v-if="currentwrnLogDetail.nowTm">{{currentwrnLogDetail.nowTm +'--'+currentwrnLogDetail.warnTm+' '+currentwrnLogDetail.nm}} </p>
              </el-card>
            </el-timeline-item>
            <el-timeline-item icon="el-icon-tickets" type='primary' size='large' :timestamp="currentwrnLogDetail.warnTm?currentwrnLogDetail.warnTm:''" placement="top">
              <el-card>
                <h4>产生预警</h4>
                <p>{{currentwrnLogDetail.warnContent?currentwrnLogDetail.warnContent:''}}</p>
              </el-card>
            </el-timeline-item>
            <el-timeline-item icon="el-icon-phone" type='primary' size='large' :timestamp="currentwrnLogDetail.noticeTm?currentwrnLogDetail.noticeTm:''" placement="top">
              <el-card>
                <h4>预警通知</h4>
                <p v-for="(notice, index) in currentwrnLogDetail.notices">{{notice}}</p>
              </el-card>
            </el-timeline-item>
            <el-timeline-item icon="el-icon-s-promotion" type='primary' size='large' :timestamp="currentwrnLogDetail.pubTm?currentwrnLogDetail.pubTm:''" placement="top">
              <el-card>
                <h4>预警发布</h4>
                <div v-for="(pubMsgs, index) in currentwrnLogDetail.pubMsgsGroup">
                  <p>内容:{{pubMsgs.content}} </p>
                  <p>接收人:{{pubMsgs.receiveUser}} </p>
                </div>
              </el-card>
            </el-timeline-item>
            <el-timeline-item icon="el-icon-close" type='primary' size='large' :timestamp="currentwrnLogDetail.closeTm?currentwrnLogDetail.closeTm:''" placement="top">
              <el-card>
                <h4>预警关闭</h4>
                <p>{{currentwrnLogDetail.closerName}}</p>
              </el-card>
            </el-timeline-item>
          </el-timeline>
        </div>
        <div class="leftBox width100" v-if="dialogNavIndex == 1" style="padding:0px">
          <div class="lrControl">
            <div class="lrLeft">
              <ul>
