<script>

import workSimulation from "../operaAndmaint/workSimulation"; //工况模拟
import processLog from "./alarmDialog/processLog"; //流程日志
import historicalData from "./alarmDialog/historicalData"; //历史数据
import upAndDownData from "./alarmDialog/upAndDownData"; //上下游数据
import operationsMaintenance from "./alarmDialog/operationsMaintenance"; //运维情况
import operationalRecords from "./alarmDialog/operationalRecords"; //操作记录

export default {
  name: "alarmManage",
  components: {
    workSimulation,
    processLog,
    historicalData,
    upAndDownData,
    operationsMaintenance,
    operationalRecords
  },
  data() {
    return {
      dialogNav: [
        { name: "报警信息" },
        { name: "远程解决" },
        { name: "报警关闭" }
      ], //报警处理弹框顶部导航
      lrControlNav: [
        { name: "预警设置" },
        { name: "预警发布" },
        { name: "历史预警" },
        { name: "历史过程" },
        { name: "水位剖面" },
        { name: "运维数据" },
        { name: "操作记录" }
      ],
      currentData: [],
      currentPage: 1, //分页组件当前页
      total: 0, //分页总条数
      pageSize: 5, //分页每页多少条数据
      pageList: [5, 10, 15, 20], //设置每页多少条
      tableData: [],



      lrControlNavIndex: 0,
      dialogNavIndex: 0, //报警处理弹框顶部导航索引
      policeRadio: "1", //是否报警
      diacrisisReason: "", //诊断原因
      showalarmDialog: false, //报警处理弹框
      showwrmDialog: false, //预警处理弹框
      currentwrnLogDetail: {},

      dateRange: "",
      typeSelect: "",
      optionData: [],
      duration: "",
      useTime: "12小时32分钟", //本周报警平均处理用时绑定数据
      taskNumber: "62", //本周处理完成报警任务数绑定数据
      alarmNumber: "8", //待处理报警数
      stateRadio: 1,
      alarmList:[{}]
    };
  },
  created() {
    this.$store.commit("setShowlefttree", false);
    this.dialogNavIndex = 0;
  },
  mounted() {
    this.geywrnList(1, 10);

  },


  methods: {

    showwrmDialogFrim(wrmid) {

      this.dialogNavIndex = 0;
      var _seit = this;
      _seit.$http.get("./static/iot/warn_mgmt/api/wrn-service/wrnLogDetail.json?warnId=" + wrmid, {}).then((response) => {
 
        var stcdsoption = [];
        for (var i = 0; i < response.data.data.stcds.length; i++) {
          stcdsoption.push({ value: response.data.data.stcds[i], label: response.data.data.stnms[i] });
        }

        _seit.currentwrnLogDetail = response.data.data;
        _seit.currentwrnLogDetail.stcdsoption = stcdsoption;
    
      }).catch((error) => {
        console.log(error)
      });

      this.showwrmDialog = true;
    },

    paginationChange(val) {
      //分页页码改变时执行
      this.currentPage = val;
      this.getcurrentData();
    },
    paginationSizeChange(val) {
      //分页pageSize改变时执行
      this.currentPage = 1;
      this.pageSize = val;
      this.getcurrentData();
    },

    getcurrentData() {
      let kaishi = (this.currentPage - 1) * this.pageSize
      let jieshu = (this.currentPage) * this.pageSize
      this.currentData = this.tableData.slice(kaishi, jieshu)


    },

    geywrnList(page, pagesize) {
      var _seit = this;
      _seit.$http.get("./static/iot/warn_mgmt/api/wrn-service/wrn_wrn_no_close_page.json?grp=wrn&status=unclose&rows=" + pagesize + "&page=" + page, {}).then((response) => {


        _seit.tableData = [];
        for (var i = 0, l = response.data.data.length; i < l; i++) {
          var item = response.data.data[i];
          item.statusstr = _seit.renderStatus(item.status)
          _seit.tableData.push(item)
        }

        _seit.total = response.data.data.length;
        _seit.getcurrentData();





      }).catch((error) => {
        console.log(error)
      });


    },

    renderStatus: function(value) {
      if (value == 'init')
        return '待处理';
      else
        return '已响应';
    },






    dialogNavClick(key) {
      this.dialogNavIndex = key;
    },
    lrNavClick(key) {
      this.lrControlNavIndex = key;
    }
  }
};

</script>
<style scoped lang="less">
.alarm-management {
  height: 100%;

  .alarm-statistics-list {
    margin-top: 6px;

    li {
      float: left;
      border: 1px solid #d9e6f7;
      width: calc(33.33% - 4px);
      margin-right: 6px;
      text-align: center;

      &:last-child {
        margin-right: 0;
      }

      .alarm-info {
        display: inline-block;
        padding: 16px 0;
        text-align: left;

        .title {
          color: #666666;
          line-height: 24px;
          margin-bottom: 4px;
        }

        .content-text {
          font-size: 22px;
          line-height: 30px;

          i {
            color: #166dce;
          }

          .iconfont {
            font-size: 22px;
          }
        }
      }
    }
  }

  .alarm-modular {
    height: calc(100% - 0px);
    margin-top: 6px;

    .modular-toolbar {
      float: left;
    }
  }
}

.modular-box {
  border: 1px solid #dce9fa;

  .modular-head {
    padding: 7px 12px;
    border-bottom: 1px solid #dce9fa;
    line-height: 30px;

    .title {
      font-size: 16px;
    }

    .title:before {
      content: "";
      display: inline-block;
      width: 3px;
      height: 10px;
      background: #166bce;
      margin: 0 6px 1px 0;
    }
  }

  .modular-body {
    height: calc(100% - 0px);

    .alert-list {
      padding: 0 16px;

      &>li {
        padding: 5px 100px;
        border-bottom: 1px solid #dce9fa;

        .el-col {
          line-height: 26px;
          color: #666666;
          padding-right: 32px;
          position: relative;

          &:first-child {
            padding-left: 28px;
          }
        }

        .site-icon {
          position: absolute;
          width: 20px;
          height: 29px;
          background: url("../../assets/images/state-icon.png") no-repeat;
          left: 0;
          top: 0;
        }

        .content-text {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }

        .site-name {
          font-size: 16px;
          color: #333;
          font-weight: 700;
        }

        .label-text {
          color: #999999;
        }

        .progress-wrap {
          padding-top: 10px;

          .node-progress {
            padding-right: 15px;
            padding-left: 9px;
            position: relative;

            .first.spot {
              width: 0;
            }

            .spot {
              display: block;
              float: left;
              height: 3px;
              width: 25%;
              background: #d1def0;
              position: relative;

              &:before {
                content: "";
                position: absolute;
                width: 9px;
                height: 9px;
                border-radius: 50%;
                background: #d1def0;
                top: -3px;
                right: 0;
              }
            }

            .spot-active-wrap {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              z-index: 2;
              padding-left: 9px;
              padding-right: 15px;

              &>span {
                display: block;
                float: left;
                width: 25%;
                height: 3px;
                position: relative;

                &:first-child {
                  width: 0;
                }
              }

              .spot-active {
                position: absolute;
                width: 9px;
                height: 9px;
                border-radius: 50%;
                right: 0;
                top: -3px;
                cursor: pointer;
              }
            }

            .stateA.spot-active-wrap {
              &>span {
                background: #ff3333;
              }

              .spot-active {
                background: #ff3333;
              }
            }

            .stateB.spot-active-wrap {
              &>span {
                background: #166bce;
              }

              .spot-active {
                background: #166bce;
              }
            }

            .stateC.spot-active-wrap {
              &>span {
                background: #36c25e;
              }

              .spot-active {
                background: #36c25e;
              }
            }
          }
        }

        .label-state {
          display: inline-block;
          vertical-align: top;
          width: 70px;
          height: 24px;
          line-height: 22px;
          text-align: center;
          border: 1px solid #166bce;
          border-radius: 12px 0;
          font-size: 12px;
          color: #166bce;
        }

        .label-state-wrap {
          padding-top: 8px;
        }

        .state-a {
          color: #36c25e;
          border-color: #36c25e;
        }

        .state-b {
          color: #ff3333;
          border-color: #ff3333;
        }
      }

      &>li:last-child {
        border-bottom: none;
      }
    }
  }
}

.state-radio-wrap {
  display: inline-block;
  vertical-align: top;
}

.search-box {
  display: inline-block;
  vertical-align: top;
  position: relative;

  height: 30px;
  line-height: 30px;

  .icon-sousuo {
    position: absolute;
    right: 10px;
    top: 0;
    color: #7c8fa4;
    cursor: pointer;
    font-size: 14px;
    z-index: 2;
  }

  .icon-sousuo:hover {
    color: #166bce;
  }

  .icon-sousuo:active {
    color: #1056a7;
  }

  .search-input {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    top: 0;
    left: 0;
    padding-left: 5px;
    padding-right: 32px;
    outline: none;
    border: 1px solid #c6d1de;
  }
}

//报警处理弹框
.head_title {
  >p {
    >span {
      display: inline-block;
      cursor: pointer;
      width: 73px;
      height: 24px;
      text-align: center;
      font-size: 13px;
      font-weight: 400;
      line-height: 24px;
      border: 1px solid #b7d7f7;
      background: #e6f1fc;
      color: #166bce;
    }
  }

  .t_nav {
    padding: 0 20px;
    margin-bottom: 12px;

    li {
      float: left;
      cursor: pointer;
      font-size: 13px;
      height: 25px;
      line-height: 25px;
      font-weight: 400;
      margin-right: 355px;
      position: relative;
      color: #999;

      &:after {
        content: "";
        height: 2px;
        width: 354px;
        background: #c6d1de;
        position: absolute;
        left: 86px;
        top: 12px;
      }

      >i {
        display: inline-block;
        height: 20px;
        width: 20px;
        color: #fff;
        line-height: 20px;
        border-radius: 50%;
        background: #c6d1de;
        margin-right: 6px;
        font-style: normal;
      }

      &:last-child {
        margin-right: 0px;

        &:after {
          display: none;
        }
      }
    }

    .current {
      color: #1a1a1a;

      >i {
        background: #166bce;
      }
    }
  }
}

.dialogContent {
  height: 450px;
  background: #eff3f5;

  .c_modul {
    li {
      margin-bottom: 12px;

      .fSpan {
        display: inline-block;
        width: 115px;
        text-align: right;
        height: 30px;
        line-height: 30px;
        color: #1a1a1a;
        vertical-align: top;
      }

      .rDiv {
        display: inline-block;
        height: 30px;
        line-height: 30px;
        width: calc(100% - 122px);
      }
    }
  }

  >div {
    float: left;
  }

  .leftBox {
    margin-right: 6px;
    height: 100%;
    background: #fff;
    border: 1px solid #d9e6f7;
    width: calc(100% - 181px);
    padding: 15px;
    overflow: auto;

    .lrControl {
      height: 100%;

      .lrLeft {
        width: 120px;
        float: left;
        border-right: 1px solid #d9e6f7;
        height: 100%;
        background: #f5f5f5;
        padding: 16px 0 0 5px;

        li {
          height: 40px;
          line-height: 40px;
          cursor: pointer;
          padding-left: 26px;

          margin-bottom: 15px;
          position: relative;
          width: 112px;

          >i {
            display: none;
          }
        }

        .currentLi {
          background: #fff;
          width: 113px;
          margin-left: 2px;
          border-radius: 5px 0 0 5px;
          border: 1px solid #d9e6f7;
          border-right: 1px solid #fff;
          color: #166bce;
          font-weight: 700;

          >i {
            position: absolute;
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            border: 1px solid #166bce;
            left: 10px;
            top: 15px;

            &::before {
              content: "";
              width: 4px;
              height: 4px;
              border-radius: 50%;
              border: 1px solid #166bce;
              position: absolute;
              left: 1px;
              top: 1px;
            }
          }
        }
      }

      .lrRight {
        width: calc(100% - 120px);
        float: left;
        height: 100%;

        .lrModel {
          padding: 12px;
        }
      }
    }
  }

  .rightBox {
    background: #fff;
    width: 175px;
    height: 100%;
    border: 1px solid #d9e6f7;

    >p {
      height: 33px;
      line-height: 33px;
      padding-left: 28px;
      border-bottom: 1px solid #d9e6f7;
      position: relative;

      &:before {
        position: absolute;
        content: "";
        width: 3px;
        height: 10px;
        background: #166bce;
        left: 10px;
        top: 12px;
      }
    }

    .timeline {
      padding: 12px 5px;
    }
  }
}

.toorbar-wrap {
  text-align: center;
  padding-top: 20px;

  .el-button {
    display: inline-block;
    vertical-align: middle;
  }
}

</style>



